.locate-position {
    position: relative;
    top: 40px;
}

.note-container {
    width: 590px;
    margin: 10px auto;
    padding: 30px 0;
    background: #ffffff;
    font-size: 14px;
    -webkit-box-shadow: 0 0 1px 0 #000000;
            box-shadow: 0 0 1px 0 #000000;
}

.wrapper {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-bottom: 1px solid #ccc;
    padding: .5em 0;
}

.wrapper:first-child {
    border-top: 1px solid #ccc;
}

.text-wrapper {
    line-height: 15px;
}

.text-wrapper a {
    text-decoration: none;
}

.text-wrapper a.ellipsis:after {
    content: '...'
}

.text-wrapper a:hover {
    text-decoration: underline;
}

.small {
    font-size: 12px;
    color: #808080;
}

.space {
    padding: .3em .7em;
}

.text-wrapper {
    padding-top: .1em;
}

